<template>
  <div class="search">
    <!-- 搜索有结果时 -->
    <div class="location" style="display:none;">
      <div class="md">
        <div class="left">
          <router-link to="">首页</router-link>>
          "<span>土豆</span>" 共 <span>3</span> 个结果
          
        </div>
      </div>
    </div>
    <section class="list" style="display:none;">
      <div class="md">
        <div class="part" v-for="part in parts">
          <div class="wb">
            <div class="tx">
              <div class="img"><img :src="part.imgsrc" alt=""></div>  
            </div>
            <h2>{{part.nickname}}</h2>
            <div class="num">
              <p class="numart">报告数<span>{{part.numart}}</span></p>
              <p class="numfan">粉丝量<span>{{part.numfan}}</span></p>
            </div>
            <p class="detail">{{part.pdetails}}</p>
            <input type="button" value="关注">
          </div>
        </div>
        <div class="clear"></div>
      </div>
    </section>

    <!-- 搜索无结果时 -->
    <div class="location-none">
      <div class="md">
        <div class="left">
          <router-link to="">首页</router-link>>
          "<span>土豆</span>" 共 <span>0</span> 个结果
          
        </div>
      </div>
    </div>
    <section class="list-none">
      <div class="md">
        <div class="none">
          <p>抱歉，没有找到“ <span>土豆</span> ”相关的内容  您可以尝试更换搜索词</p>
        </div>
      </div>
    </section>


    <!-- <section class="pagination">
      <div class="md">
        <el-pagination
          layout="prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>
    </section> -->
  </div>
</template>

<script>
// import pageination from "../../../static/js/pageination.js"
export default {
  name:"gsearch",
  data(){
    return{
      parts:[
        {
          imgsrc:require("../../assets/images/upload4.png"),
          nickname:"灰土豆",
          numart:69,
          numfan:169,
          pdetails:'创办国内第一家视频测评机构ZEALEDR',
        },
        {
          imgsrc:require("../../assets/images/upload4.png"),
          nickname:"灰土豆",
          numart:69,
          numfan:169,
          pdetails:'创办国内第一家视频测评机构ZEALEDR',
        },
        {
          imgsrc:require("../../assets/images/upload4.png"),
          nickname:"灰土豆",
          numart:69,
          numfan:169,
          pdetails:'创办国内第一家视频测评机构ZEALEDR',
        },
        {
          imgsrc:require("../../assets/images/upload4.png"),
          nickname:"灰土豆",
          numart:69,
          numfan:169,
          pdetails:'创办国内第一家视频测评机构ZEALEDR',
        },
      ],
    }
  },

  
}
</script>

<style lang="scss" scoped>
.search{
  section.banner{
    height: 304px;
    position: relative;
    img{width:100%;position: absolute;}
    .wb{
      top:50%;
      left: 50%;
      transform: translate(-50%,-50%);
      position: absolute;
      text-align: center;
      letter-spacing: 1px;
      h1{
        font-size: 48px;
        color: #fff;
        letter-spacing: 4px;
      }
      p{
        margin-top:28px;
        letter-spacing: 4px;
        font-family: "MicrosoftYaHeiLight";
        font-size: 24px;
        color:#fff;
        b{
          font-family: "MicrosoftYaHei-Bold";
          letter-spacing: 4px;
          font-size: 24px;
        }
      }
    }
  }
  // 目前所在位置
  .location{
    height: 54px;
    line-height: 97px;
    .md{
      width:1200px;
      margin:0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left{
        color:#999;
        font-size: 16px;
        a{
          color: #999;
          font-size: 16px;
          padding:0 12.5px;
        }
        span{
          font-size: 16px;
          color: #EA6457;
        }
      }
      .right{
        a{
          font-size: 14px;
          color: #333;
          margin-left: 25px;
        }
        .active{
          width:100px;
          height: 35px;
          line-height: 35px;
          display: inline-block;
          text-align: center;
          background: #EA6457;
          color: #fff;
          border-radius: 5px;
        }
      }
      
    }
  }
  // 达人列表
  section.list{
    padding-top:35px;
    background: #F9F9F9;
    .md{
      width:1200px;
      margin:0 auto;
      .part{
        width:378px;
        height: 348px;
        text-align: center;
        float: left;
        margin-right: 30px;
        margin-bottom:45px;
        background: #fff;
        border:1px solid rgba(225,226,228,0.10);
        box-shadow: 0px 1px 3px rgba(0,0,0,0.1);
        border-radius: 5px;
        &:hover{
          transition: all 0.2s;
          transform:scale(1.05);
          .wb{
            input[type="button"]{
              color:#fff;
              background: #EA6457;
            }
          }
          
        }
        &:nth-child(3n){
          margin-right: 0;
        }
        .wb{
          .tx{
            display: inline-block;
            margin:48px 0 20px 0;
            .img{
              width:90px;
              height: 90px;
              border-radius: 50%;
              overflow: hidden;
              img{
                width:100%;
                height:100%;
              }
            }
          }
          h2{
            font-size: 24px;
            color:#191919;
          }
          .num{
            margin:15px 0 25px 0;
            p{
              padding:0 10px;
              font-size: 12px;
              color: #444;
              display: inline-block;
              span{
                padding:0 2px;
                color: #EA6457;
              }
            }
            p.numart{
              border-right:1px solid #d1d1d1;
            }
          }
          p.detail{
            font-size: 14px;
            color: #999;
          }
          input[type="button"]{
            width:150px;
            height: 35px;
            margin-top:35px;
            font-size: 12px;
            background: #fff;
            color:#989898;
            border:1px solid #ededed;
            border-radius: 5px;
            
          }
        }
      }
      .none{
        p{
          width:295px;
          margin: 0 auto;
          line-height: 24px;
          font-size: 16px;
          color: #999;
          span{
            font-size: 16px;
            color: #EA6457;
          }
        }
      }
    }
  }
  .location-none{
   
    .md{
      width:1200px;
      margin:0 auto;
      height: 97px;
      line-height: 97px;
      display: flex;
      border-bottom:1px solid #c6c6c6;
      justify-content: space-between;
      align-items: center;
      .left{
        color:#999;
        font-size: 16px;
        a{
          color: #999;
          font-size: 16px;
          padding:0 12.5px;
        }
        span{
          font-size: 16px;
          color: #EA6457;
        }
      }
      .right{
        a{
          font-size: 14px;
          color: #333;
          margin-left: 25px;
        }
        .active{
          width:100px;
          height: 35px;
          line-height: 35px;
          display: inline-block;
          text-align: center;
          background: #EA6457;
          color: #fff;
          border-radius: 5px;
        }
      }
      
    }
  }
   section.list-none{
    padding-top:35px;
    background: #F9F9F9;
    .md{
      width:1200px;
      margin:0 auto;
      .none{
        min-height: 600px;
        p{
          width:295px;
          margin: 0 auto;
          line-height: 24px;
          font-size: 16px;
          color: #999;
          span{
            font-size: 16px;
            color: #EA6457;
          }
        }
      }
    }
  }
  .pagination{
    .md{
      width:1200px;
      margin:0 auto;
      text-align: center;
      .el-pagination{
        margin-bottom:80px;
        color: #202020;
        .el-pager{
          li.number{
            border:1px solid #ebebeb;
            
          }
          li.active{
            background: #f9f9f9!important;
          }
        }

      }
      
    }
  }
}
</style>





